"use client";

import { useState, useEffect } from "react";
import styles from "@/app/styles/componentStyles/headerNav.module.css";

export function HeaderNav() {
  const [isOpen, setIsOpen] = useState(false);

  const toggleMenu = () => {
    setIsOpen((prev) => !prev);
  };

  useEffect(() => {
    if (isOpen) {
      document.body.addEventListener("click", closeMenu);
    } else {
      document.body.removeEventListener("click", closeMenu);
    }

    return () => {
      document.body.removeEventListener("click", closeMenu);
    };
  }, [isOpen]);

  const closeMenu = (event: MouseEvent) => {
    const target = event.target as HTMLElement;
    if (!target.closest(`.${styles.nav}`)) {
      setIsOpen(false);
    }
  };

  return (
    <nav className={styles.nav}>
      {/* –ë—É—Ä–≥–µ—Ä-–∫–Ω–æ–ø–∫–∞ */}
      <button className={styles.burger} onClick={(e) => { e.stopPropagation(); toggleMenu(); }}>
        <span></span>
        <span></span>
        <span></span>
      </button>

      {/* –ú–µ–Ω—é */}
      <ul className={`${styles.list} ${isOpen ? styles.open : ""}`}>
        <li><a href="#" className={styles.link}>–ì–ª–∞–≤–Ω–∞—è</a></li>
        <li><a href="#" className={styles.link}>–û –Ω–∞—Å</a></li>
        <li><a href="#" className={styles.link}>–ö–æ–Ω—Ç–∞–∫—Ç—ã</a></li>
      </ul>
    </nav>
  );
}
